// src/app/app/page.tsx
"use client";

import { useSession } from "next-auth/react";
import Link from "next/link";
import {
  ListBulletIcon,
  PencilIcon,
  BookOpenIcon,
  ClockIcon,
  ArrowUpCircleIcon,
  SparklesIcon,
  PhotoIcon,
  CheckCircleIcon,
  DevicePhoneMobileIcon,
} from "@heroicons/react/24/outline";
import { useTier } from "@/lib/db/useTier";
import { motion } from "framer-motion";
import { itemVariant } from "@/lib/animations"; // Assuming you have this from before

// --- COMPONENT FOR FREE USERS (The "Sexy" Upsell) ---
function UpgradeSection() {
  const proFeatures = [
    {
      icon: CheckCircleIcon,
      name: "Go Unlimited",
      description: "Remove all limits on tasks, notes, and Pomodoro history.",
      bg: "bg-green-50",
      iconColor: "text-green-600",
    },
    {
      icon: SparklesIcon,
      name: "AI Weekly Insights",
      description:
        "Get AI-powered summaries of your productivity and diary entries.",
      bg: "bg-purple-50",
      iconColor: "text-purple-600",
    },
    {
      icon: PhotoIcon,
      name: "Image Uploads",
      description:
        "Visually organize your notes by attaching images and files.",
      bg: "bg-blue-50",
      iconColor: "text-blue-600",
    },
    {
      icon: DevicePhoneMobileIcon,
      name: "Sync More Devices",
      description:
        "Keep your workflow in sync across all your devices, not just one.",
      bg: "bg-sky-50",
      iconColor: "text-sky-600",
    },
  ];

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1, // Stagger children animation
      },
    },
  };

  const cardVariant = {
    hidden: { opacity: 0, y: 30, scale: 0.95 },
    visible: {
      opacity: 1,
      y: 0,
      scale: 1,
      transition: { duration: 0.4, ease: "easeOut" },
    },
  };

  return (
    <div className="space-y-12">
      {/* 1. The Main "Hero" CTA */}
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
        className="relative overflow-hidden rounded-xl border border-gray-200 bg-gradient-to-tr from-blue-600 to-indigo-700 p-8 shadow-lg"
      >
        <div className="relative z-10 text-center">
          <SparklesIcon className="h-12 w-12 text-white/80 mx-auto mb-4" />
          <h2 className="text-3xl font-bold text-white mb-3">
            Unlock Your Full Potential
          </h2>
          <p className="text-lg text-blue-100 max-w-lg mx-auto mb-8">
            You&apos;re using the Free plan. Upgrade to Pro to remove all limits
            and get access to powerful AI features.
          </p>
          <motion.div
            // Pulsing "Get Pro" button
            animate={{ scale: [1, 1.03, 1] }}
            transition={{ duration: 1.5, repeat: Infinity, ease: "easeInOut" }}
          >
            <Link
              href="/app/pricing" // This is the "how to buy" link
              className="inline-flex items-center justify-center gap-2 px-8 py-3 bg-white text-blue-700 font-semibold rounded-lg shadow-md hover:bg-gray-100 transition-all duration-200 transform hover:scale-105"
            >
              <ArrowUpCircleIcon className="h-5 w-5" />
              View Pro Plans
            </Link>
          </motion.div>
        </div>
        {/* Subtle background glow */}
        <div className="absolute top-0 left-1/2 -translate-x-1/2 w-1/2 h-full bg-white/10 rounded-full blur-3xl opacity-50"></div>
      </motion.div>

      {/* 2. "What You're Missing" Section */}
      <div>
        <h2 className="text-2xl font-semibold text-gray-900 mb-6">
          Why Go Pro?
        </h2>
        <motion.div
          variants={containerVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.1 }}
          className="grid grid-cols-1 gap-6 sm:grid-cols-2"
        >
          {proFeatures.map((feature) => (
            <motion.div
              key={feature.name}
              variants={cardVariant}
              whileHover={{ scale: 1.03, y: -5 }} // Sexy hover effect
              transition={{ type: "spring", stiffness: 300 }}
              className={`relative flex gap-x-5 rounded-xl p-6 bg-white border border-gray-200 shadow-sm transition-all duration-300 hover:shadow-lg hover:border-blue-300 cursor-pointer`}
            >
              <div className={`flex-shrink-0 p-3 ${feature.bg} rounded-lg`}>
                <feature.icon
                  className={`h-6 w-6 ${feature.iconColor}`}
                  aria-hidden="true"
                />
              </div>
              <div>
                <h3 className="text-lg font-semibold text-gray-900">
                  {feature.name}
                </h3>
                <p className="mt-1 text-sm text-gray-600">
                  {feature.description}
                </p>
              </div>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </div>
  );
}

// --- COMPONENT FOR PAID USERS (Clean Hub) ---
function ProHub() {
  const tier = useTier(); // Get the tier name (e.g., "basic", "pro")

  const quickLinks = [
    {
      name: "My Tasks",
      href: "/app/tasks",
      icon: ListBulletIcon,
      description: "View all your to-dos",
    },
    {
      name: "My Notes",
      href: "/app/notes",
      icon: PencilIcon,
      description: "Capture a new idea",
    },
    {
      name: "My Diary",
      href: "/app/diary",
      icon: BookOpenIcon,
      description: "Write today's entry",
    },
    {
      name: "Focus Timer",
      href: "/app/pomodoro",
      icon: ClockIcon,
      description: "Start a focus session",
    },
  ];

  return (
    <div className="space-y-10">
      {/* Tier Status Card */}
      <div className="rounded-lg border border-green-200 bg-gradient-to-r from-green-50 to-emerald-50 p-6 shadow-sm">
        <h3 className="text-xl font-semibold text-green-900">
          Welcome to TaskGlyph{" "}
          {tier ? `${tier.charAt(0).toUpperCase() + tier.slice(1)}` : ""}!
        </h3>
        <p className="mt-1 text-green-700">
          You have full access to all features. Thank you for your support!
        </p>
      </div>

      {/* Quick Links Grid (Useful for paid users) */}
      <div>
        <h2 className="text-xl font-semibold text-gray-900 mb-4">
          Quick Access
        </h2>
        <motion.div
          initial="hidden"
          animate="visible"
          variants={{
            visible: { transition: { staggerChildren: 0.05 } },
          }}
          className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-4"
        >
          {quickLinks.map((link) => (
            <motion.div
              key={link.name}
              variants={itemVariant}
              whileHover={{ scale: 1.05 }}
              transition={{ type: "spring", stiffness: 400 }}
            >
              <Link
                href={link.href}
                className="group relative block p-6 bg-white rounded-lg shadow-md border border-gray-200 hover:shadow-xl hover:border-blue-500 transition-all duration-300"
              >
                <span className="p-3 bg-gray-100 text-gray-700 rounded-lg inline-flex group-hover:bg-blue-100 group-hover:text-blue-600 transition-colors">
                  <link.icon className="h-6 w-6" />
                </span>
                <h3 className="mt-4 text-lg font-semibold text-gray-900">
                  {link.name}
                </h3>
                <p className="mt-1 text-sm text-gray-500">{link.description}</p>
              </Link>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </div>
  );
}

// --- MAIN PAGE COMPONENT ---
export default function AppHomePage() {
  const { data: session } = useSession();
  const tier = useTier();
  const isFree = tier === "free";

  // Show loading skeleton while tier is being determined
  if (!tier) {
    return (
      <div className="space-y-10 animate-pulse">
        <div className="h-10 w-2/3 bg-gray-200 rounded-lg"></div>
        <div className="h-40 w-full bg-gray-200 rounded-lg"></div>
        <div className="h-8 w-1/3 bg-gray-200 rounded-lg"></div>
        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2">
          <div className="h-36 bg-gray-200 rounded-lg"></div>
          <div className="h-36 bg-gray-200 rounded-lg"></div>
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-10">
      {/* Welcome Header */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">
          Welcome back, {session?.user?.name?.split(" ")[0] || "User"}!
        </h1>
      </div>

      {/* Render different content based on tier */}
      {isFree ? <UpgradeSection /> : <ProHub />}
    </div>
  );
}
